*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none !important;
  cursor: default;
}
.jkl {
  display: none;
}
:root {
  --p-clr: #fed605;
  --s-clr: #d8d8d8;
  --w-clr: white;
  --b-clr: black;
}
.row {
  perspective: 300;
}
.row > * {
  /* transform:translate3d(1,1,1,50deg) */
}
.navQ {
  transform: scale(1.3);
  cursor: pointer;
}
.navQ:hover {
  transform: scale(1.35);
  color: var(--s-clr);
}
.over {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: red;
  /* background: url(./leadbg.gif), url("./bg2.jpg"); */
  background-blend-mode: multiply;
  background-size: 200px;
  /* background-repeat: no-repeat; */
  z-index: -1;
  opacity: 0.2;
  animation: over 1s steps(2);
  animation-iteration-count: infinite;
  /* animation-duration: 0.4s steps(2); */
  animation-fill-mode: backwards;
  animation-direction: alternate;
  -o-animation-timing-function: linear;
}
