/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: black;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--s-clr);
}
@font-face {
  font-family: "Beacon";

  src: url("./Fonts/Poppins-Thin.otf");
}

@font-face {
  font-family: "Poppins";
  src: url(./Fonts/Poppins.otf);
}
@font-face {
  font-family: "Beacon";
  src: url(./Fonts/Helvetica.ttf);
}
a {
  text-decoration: none;
  color: inherit;
}
.dis60 {
  display: block;
}
#root {
  /* mix-blend-mode: multiply; */
}
html {
  /* 
  scroll-snap-type: y proximity;
  overflow-y: scroll; */
}
body {
  font-size: 1rem;
  font-family: "Poppins", sans-serif;
  background-color: var(--p-clr);
  background-color: var(--b-clr);
  width: 100%;
  height: 100%;
  background: url("bg.jpg");
  overflow-x: hidden;
  /* background-attachment: fixed; */
}
.row {
  width: 100%;
  /* margin-inline: auto; */
}
.container {
  max-width: 1440px;

  margin-inline: auto;
}

h1,
h2 {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  font-family: "Beacon";

  color: #cd0101 !important;
  /* text-shadow: 2px 0 0px var(--b-clr); */
  /* filter: drop-shadow(4px 0 0 var(--b-clr)); */
  letter-spacing: 2px;
  word-spacing: 0;
}
img {
  object-fit: contain;
  /* display: none !important; */
}
button {
  all: revert;
}
input,
textarea {
  padding: 0.5rem;
  border: 1px var(--s-clr) solid;
  font-size: 1.1rem;
}
label {
  margin: 0.4rem 0;
  cursor: pointer;
}
input[type="radio"] {
  margin-top: 0.4rem;
  transform: scale(1.5);
  width: fit-content;
}
p.lead > a {
  color: var(--s-clr);
}
.nav-toggle {
  display: none;
}
.bg-white {
  /* background: linear-gradient(0deg, transparent 30%, var(--w-clr) 14%); */
  background-color: transparent;
  background-position: bottom;
  background-size: 100% 90%;
  /* box-shadow: 0 0 2rem gray inset; */
}
.bg-yellow {
  /* background: linear-gradient(0deg, transparent 30%, var(--p-clr) 14%); */
  background-color: var(--p-clr);
  background-color: transparent;

  background-position: bottom;
  background-size: 100% 90%;
}
.bg-dark {
  background-color: #000000;
  background-color: transparent;

  background-position: bottom;
  background-size: 100% 90%;
}
.reverse-section-flex {
  flex-direction: row-reverse;
}

.lead {
  margin-bottom: 1.5rem;
  /* background: url(./lead.jpg); */
  background-size: 200px;
  font-weight: 600;
  font-size: 1.1rem;
  width: 400px;
  letter-spacing: 1px;
  font-family: "Beacon";

  font-weight: 100;
  /* background-clip: text; */
  color: rgb(204, 202, 202) !important;
  -webkit-backdrop-filter: text;
}
section {
  min-height: 100vh;
  /* overflow: hidden; */
  max-height: 100%;
  padding: 1rem 1.5rem;
  /* background: url('bg1.jpg') !important; */
}
.smooth-scroll {
  scroll-snap-align: start;
}
.smooth-scroll-center {
  scroll-snap-align: center;
}
.section-flex-wrapper {
  display: flex;
  align-items: center;
}
.section-flex-wrapper > div:nth-child(2) {
  display: grid;
  place-content: center;
  padding-left: 2rem;
  flex-basis: 70%;
}
.section-flex-wrapper > div:nth-child(1) {
  position: relative;
  flex-basis: 70%;

  /* top: -4rem; */
}
/* section > *{border: 5px gray solid;} */
.btn {
  /* border-radius: 50%; */
  min-width: 125px;
  max-width: 100%;
  flex-basis: 125px;
  font-size: 1.1rem;
  text-align: center;
  height: 45px;
  display: flex;
  overflow: hidden;
  align-items: center;
  /* margin: 1rem 0.6rem; */
  white-space: nowrap;
  display: flex;
  align-items: stretch;
  position: relative;
  display: inline-block;
  margin-right: 0.5rem;
  border: 2px transparent solid;
}

.btn a > *,
.btn > *,
.btn {
  transition: 0.3s !important;
  transition-timing-function: cubic-bezier(0.43, 0.22, 0.4, 1.46);
}

/* .btn:hover > span ,.btn:hover{background-color: black;color: white;} */
.btn:hover {
  /* border-color: var(--p-clr); */
}
.btn span {
  display: inline-block;
  /* background: white; */
  cursor: pointer;
  position: relative;
  z-index: 2;
  padding: 0.5rem 1rem;
  width: 125px;
}
.btn:hover .btn-arrow {
  margin-left: 0rem;
  /* position: relative; */
  padding-right: 1rem;
  /* transition: 0.3s; */
  /* transition-timing-function: cubic-bezier(0.43, 0.22, 0.4, 1.46); */
  /* transition-timing-function: 1s; */
  /* background-color: red; */
}
.btn-primary span {
  background: var(--s-clr);
  background: #cd0101;
}
.btn-secondary span {
  background: var(--w-clr);
  background-color: white;
}
.btn span {
  position: relative;
  /* top: 3px; */
  font-family: "Beacon";

  font-weight: 900;
}
.btn-primary {
  background-color: var(--s-clr);
  background: #cd0101;

  color: var(--w-clr);
}

.btn-secondary {
  background-color: white;
  color: black;
  border: 1px black solid;
  color: black;
}

.btn-group {
}
.btn-group > .btn {
  /* width: 200px; */
}
/* Header */

.header {
  position: sticky;
  z-index: 3;
  top: 0;
  background-color: var(--p-clr);
  background-color: transparent;
  color: var(--w-clr);
  /* color: black; */
}

.header-wrapper {
  padding: 0.2rem;
  display: flex;
  justify-content: space-between;
  backdrop-filter: blur(2px);
  border-bottom: 1px black solid;
}
.section-flex-item.section-img-wrapper img {
  width: 100%;
  height: 100%;
}

.brand {
  padding-left: 1rem;

  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  /* border-right: 1px black solid; */
  padding-right: 0.5rem;
  display: grid;
  place-content: center;
}

.brand-logo {
  width: 30px;
  object-fit: contain;
}

.nav-list-item-wrapper {
  display: flex;
  align-items: center;
}

.nav-list-item {
  margin-right: 0.3rem;
}

.nav-list-item a {
  padding: 0.5rem 1rem;

  display: grid;
  place-content: center;
}

.nav-list-item svg {
  width: 20px;
  height: 20px;
}
.nav-list-item svg:hover {
  transform: scale(1.1);
}
.nav-list-item svg:hover path {
  fill: var(--s-clr);
}
.btn-arrow {
  display: inline-block;
  margin-left: -4rem;
  position: relative;
  top: 8px;
}
/* external css: flickity.css */

* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.carousel {
  background: #eee;
}

.carousel-cell {
  width: 28%;
  height: 200px;
  margin-right: 10px;
  background: #8c8;
  border-radius: 5px;
  counter-increment: carousel-cell;
}

.carousel-cell.is-selected {
  background: #ed2;
}

/* cell number */
.carousel-cell:before {
  display: block;
  text-align: center;
  content: counter(carousel-cell);
  line-height: 200px;
  font-size: 80px;
  color: white;
}
circle[fill="#2079FF"] {
  filter: drop-shadow(0 5px 10px rgba(0, 0, 0, 0.475));
}
